.product {
    padding: 20px 50px;
    display:flex;
    gap: 50px;

    .left {
        flex:1;
        display:flex;   
        gap:20px;
        

        .images {
            flex: 1;
            
           
            img{
                width: 100%;
                height:150px;
                object-fit:cover;   
                cursor:pointer;
                margin-bottom:10px;            
            }
        }

        .mainImage {
            flex: 5;
            img{
            width:100%;
            max-width: 800px;
            min-height:900px;
            object-fit: cover;
            }
        }
    }

    .right{
        flex:1;
        display:flex;
        flex-direction: column;
        gap:30px;
        .price{
            font-size: 30px;
            color: blue;
            font-weight: 500;

        }
        p{
            font-size: 18px;
            font-weight: 300;
            text-align: justify;
        }
        .quantity{
            display:flex;
            align-items: center;
            gap:10px;
            button{
                width:50px;
                height:50px;
                display:flex;
                align-items:center;
                justify-content: center;
                cursor: pointer;
                border:none;
            }
        }
        .add{
            width:250px;
            padding:10px;
            background-color:blue;
            color:white;
            display:flex;
            align-items: center;
            justify-content: center;
            gap:20px;
            cursor:pointer;
            border:none;
            font-weight: 500;
        }
        .links{
                display:flex;
                gap:20px;

                .item{
                    display: flex;
                    align-items: center;
                    gap:10px;
                    color: blue;
                }
            }
    }
}